<template>
  <div
    class="dropzone"
    :class="{
      'dragOver': draggingOver,
      'fileUploading': showProgress,
      'finished': finished
    }"
    ref="drop_zone"
    @drop="dropHandler"
    @dragover="dragOverHandler"
    @dragleave="dragLeaveHandler"
    @mouseup="mouseupHandler"
  >
    <div class="caption" v-html="caption"></div>
    <div v-if="!showProgress && !finished">
      <p>Drag one or more files to this <i>drop zone</i>.</p>
    </div>
    <div v-else>
      <div v-if="finished">
        finished uploading {{this.count}} files
      </div>
      <div v-else>
        <div v-for="(file, idx) in fileList">
          <span class="fileName" v-html="fileNameFormatted(file.name)"></span>
          <div class="progressBar">
            <div :style="setWidth(idx)" class="innerProgress" :key="idx"></div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'Dropzone',
  props: ['state', 'caption'],
  data(){
    return {
      testData : "it works!",
      draggingOver: false,
      showProgress: false,
      fileList: [],
      count: 0,
      finished: false,
      showUploadProgress: false,
      filesUploading: []
    }
  },
  methods:{
    setWidth(idx){
      return 'width: calc(' + this.filesUploading[idx].perc + '%)'
    },
    fileNameFormatted(name){
      return name.length <= 30 ? name:name.substring(0, 20) + '...' + name.substring(name.length-6)
    },
    dropHandler(e){
      let error = false
      this.count = 0
      e.preventDefault()
      //e.stopPropagation()
      this.filesUploading = []
      if (e.dataTransfer.items) {
        this.fileList = [];
        this.count=[...e.dataTransfer.items].length;
        this.filesUploading = Array(this.count).fill().map(v=>{return {}});
        [...e.dataTransfer.items].forEach((item, i) => {
          if (item.kind === 'file') {
            const file = item.getAsFile()
            console.log(`… file[${i}].name = ${file.name}`)
            this.showProgress = true
            this.draggingOver = false
            this.fileList = [...this.fileList, file]

            let files = this.fileList
            Array.from(files).forEach((v, i)=>{
              v.completed = false
              this.filesUploading[i].perc = 0
              this.filesUploading[i].fileName = v.name
            })
            Array.from(files).forEach((v, i)=>{
              if(error) return
              if(
                //(v.type == 'audio/mpeg' ||
                //v.type == 'audio/ogg' ||
                //v.type == 'audio/wav') &&
                v.size < 100000000
              ){
                let data = new FormData()
                data.append('name', v.name)
                data.append('userID', this.state.loggedinUserID)
                data.append('description', '')
                data.append('file', v)
                let request = new XMLHttpRequest()
                request.open('POST', this.state.baseURL + '/upload.php')
                request.upload.addEventListener('progress', e => {
                  let perc = (e.loaded / e.total)*100
                  this.filesUploading[i].fileName = v.name
                  this.filesUploading[i].perc = perc
                })
                request.addEventListener('load', e=>{
                  v.completed = true
                  let finished = true
                  Array.from(files).forEach(q=>{
                    if(!q.completed) finished = false
                  })
                  if(finished) {
                    //window.location.href = window.location.origin + '/u/' + this.state.loggedinUserName
                    this.finished = true
                    this.draggingOver = false
                    this.showProgress = false
                  }
                })
                request.send(data)
              } else {
                this.showUploadProgress = false
                this.finished = false
                this.draggingOver = false
                error = true
                alert('a file was rejected due to incorrect type or filesize (max filesize = 100MB)')
              }
            })

          }
        })
      }else{
        this.fileList = [];
        this.count=[...e.dataTransfer.files].length;
        this.filesUploading = Array(this.count).fill().map(v=>{return {}});
        [...e.dataTransfer.files].forEach((file, i) => {
          if(error) return
          this.showProgress = true
          this.draggingOver = false
          this.fileList = [...this.fileList, file]


          let files = this.fileList
          Array.from(files).forEach((v, i)=>{
            v.completed = false
            this.filesUploading[i].perc = 0
            this.filesUploading[i].fileName = v.name
          })
          Array.from(files).forEach((v, i)=>{
            if(
              //(v.type == 'audio/mpeg' ||
              //v.type == 'audio/ogg' ||
              //v.type == 'audio/wav') &&
              v.size < 100000000
            ){
              let data = new FormData()
              data.append('name', v.name)
              data.append('userID', this.state.loggedinUserID)
              data.append('description', '')
              data.append('file', v)
              let request = new XMLHttpRequest()
              request.open('POST', this.state.baseURL + '/upload.php')
              request.upload.addEventListener('progress', e => {
                let perc = (e.loaded / e.total)*100
                this.filesUploading[i].fileName = v.name
                this.filesUploading[i].perc = perc
                this.$refs['progress'+i].style.width=`calc(${perc+'%'})`
              })
              request.addEventListener('load', e=>{
                v.completed = true
                let finished = true
                Array.from(files).forEach(q=>{
                  if(!q.completed) finished = false
                })
                if(finished) {
                  //window.location.href = window.location.origin + '/u/' + this.state.loggedinUserName
                  this.finished = true
                  this.draggingOver = false
                  this.showProgress = false
                }
              })
              request.send(data)
            } else {
              this.showUploadProgress = false
              this.draggingOver = false
              this.showProgress = false
              error = true
              alert('a file was rejected due to incorrect type or filesize (max filesize = 100MB)')
            }
          })




        })
      }
    },
    dragLeaveHandler(e){
      this.draggingOver = false
      e.preventDefault()
      e.stopPropagation()
    },
    mouseUpHandler(e){
      this.draggingOver = false
      e.preventDefault()
      e.stopPropagation()
    },
    dragOverHandler(e){
      if(!this.showProgress) this.finished=false
      this.draggingOver = true
      e.preventDefault()
      e.stopPropagation()
    }
  },
  mounted(){
    console.log(this.$refs)
    window.onmousemove=()=>{
      this.draggingOver = false
    }
  }
}

</script>

<style scoped>
  .dropzone{
    padding: 20px;
    color: #fff;
    background: #333;
    display: inline-block;
    margin: 10px;width: 300px;
    height: 200px;
    border: 1px solid #4f84;
    vertical-align: top;
    overflow-x: hidden;
    overflow-y: auto;
  }
  .caption{
    font-size: 1.3em;
    color: #afa;
  }
  .fileUploading{
    background: #4f88!important;
  }
  .progressBar{
    width: 100%;
    border: 1px solid #fff6;
    height: 10px;
    margin-bottom: 10px;
  }
  .innerProgress{
    background: #8ff;
    height: 100%;
  }
  .finished{
    background: #40f2!important;
    color: #f44;
  }
  .fileName{
    color: #8f8; 
  }
  .dragOver{
    background: #0ff1!important;
  }
</style>
